import Axios, { AxiosRequestConfig } from 'axios'
const Api = 'https://b1264722-067f-47f8-9d37-d4fc9e4b7d46.mock.pstmn.io' // mock
// const Api = 'https://api.cryptonator.com/api/ticker/' // API
const axios = Axios.create({
  // 
  baseURL:Api,
  timeout:30000
})

export async function request(url: string, config?: AxiosRequestConfig) {
  const response = await axios.request({ url, ...config })
  const result = response.data
  return result
}

export default axios